
import slide from 'components/slide/index'
import http from 'js/http'
import Loading from 'vux/src/components/loading/index.vue'

import {mapState, mapMutations, mapGetters} from 'vuex'
import {hidMobile} from 'js/mixin'


export default {
name: "index",
components: {
slide,
Loading,
},
data() {
return {
vcodeIpt: '',
loadingShow: false,
fieldDisabled: false,
wait: 0,
hidMobile: hidMobile,
keys: [1, 2, 3, 4, 5, 6, 7, 8, 9, '', 0],
}
},
watch: {
/*vcodeIpt(v) {
this.vcodeIpt = v.replace(/[^\d]/g, '')
if (this.vcodeIpt.length === 6) {
this.$emit('valid', this.vcodeIpt)
}
}*/
},
computed: {
codeStr() {
if (!this.wait) {
return ''
} else {
return `${this.wait}s`
}
},
...mapGetters([
"loginMsg",
]),
},
methods: {
closeTem() {
this.$emit('close', true)
},
keyUpHandle(v) {
if (v === '' || this.vcodeIpt.length === 6) return
this.vcodeIpt = this.vcodeIpt + v
if (this.vcodeIpt.length >= 6) {
this.$emit('valid', this.vcodeIpt)
}
},
delHandle () {
if (this.vcodeIpt.length <= 0) return false
this.vcodeIpt=this.vcodeIpt.slice(0,-1)
},
getCode(){
this.wait = 60
let itv = setInterval(() => {
if (this.wait) {
this.wait--
} else {
clearInterval(itv)
}
}, 1000)

http.post('/sms/sms/transfer-pass', {}, (res) => {
if (res.code !== 0) {
this.$vux.toast.show(res.msg)
}
})
}
},
created(){
this.getCode()
}

}



