import slide from 'components/slide/bottom'

export default {
  name: "index",
  components: {
    slide,
  },
  props: {
    dataList: {
      type: Array,
      default: function () {
        // return [];//return {}
        return [
          {
            value: '1',
            label: ''
          },
          {
            value: '2',
            label: '',
            disabled: true
          }
        ]
      }
    },
    label: {
      type: String,
      default: 'label'
    },
    value: {
      type: String,
      default: 'value'
    },
    placeholder:{
      type:String,
      default:''
    }

  },
  data() {
    return {
      selected: '',
      selectedLabel: '',
      showPop: false
    }
  },
  methods: {
    openPop(){
      this.showPop = true
    },
    selectedItem(item) {
      console.log(item)
      if (item.selected === this.selected) return
      this.selected = item[this.value]
      this.selectedLabel = item[this.label]
      this.showPop = false
      this.$emit('changeSel', item)
    }
  },
  computed: {
    /*selectedLabel(){
      if (!this.selected||!this.dataList.length) return ''
    }*/
  }
}



